<template>
	<div class="ratings" v-el:ratings>
		<div class="ratings-content">
			<div class="overview">
				<div class="overview-left">
					<h1 class="score">{{seller.score}}</h1>
					<div class="title">综合评分</div>
					<div class="rank">高于周边商家</div>
				</div>
				<div class="overview-right">
					<div class="score-wrapper">
						<span class="title">服务态度</span>
						<star :size="36" :score="seller.serviceScore"></star>
						<span class="score">{{seller.serviceScore}}</span>
					</div>
					<div class="score-wrapper">
						<span class="title">商品评分</span>
						<star :size="36" :score="seller.foodScore"></star>
						<span class="score">{{seller.foodScore}}</span>
					</div>
					<div class="delivery-wrapper">
            <span class="title">送达时间</span>
            <span class="delivery">{{seller.deliveryTime}}分钟</span>
          </div>
				</div>
			</div>
			<split></split>
			<ratingselect :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="ratings"></ratingselect>
			<div class="rating-wrapper">
				<ul>
					<li v-show="needShow(rating.rateType,rating.text)" v-for="rating in ratings" class="rating-item border-1px">
						<div class="avatar">
							<img :src="rating.avatar" width="28" height="28">
						</div>
						<div class="content">
							<h1 class="name">{{rating.username}}</h1>
							<div class="star-wrapper">
								<star :size="24" :score="rating.score"></star>
                <span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}分钟</span>
							</div>
							<p class="text">{{rating.text}}</p>
              <div class="recommend" v-show="rating.recommend && rating.recommend.length">
                <span class="icon-thumb_up"></span>
                <span class="item" v-for="item in rating.recommend">{{item}}</span>
              </div>
              <div class="time">
                {{rating.rateTime | formatDate}}
              </div>
						</div>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script type="text/javascript">
	import Star from '../star/star.vue'
	import Split from '../split/split.vue'
	import ratingselect from '../ratingselect/ratingselect.vue'
	import {formatDate} from '../../common/js/date.js'
	import BScroll from 'better-scroll'
	const ALL = 2
	const ERRNO = 0
	export default{
		props: {
			seller: {
				type: Object,
				default() {
					return {}
				}
			}
		},
		data() {
			return {
				ratings: [],
				selectType: ALL,
				onlyContent: true
			}
		},
		created() {
			var self = this
			this.$http.get('/api/ratings').then((res) => {
				res = res.body
				if (res.errno === ERRNO) {
					self.ratings = res.data
					self.$nextTick(() => {
						if (!self.scroll) {
							self.scroll = new BScroll(this.$els.ratings, {
								click: true
							})
						} else {
							self.scroll.refresh()
						}
					})
				}
			})
		},
		methods: {
			needShow(type, text) {
				if (this.onlyContent && !text) {
					return false
				}
				if (this.selectType === ALL) {
					return true
				} else {
					return type === this.selectType
				}
			}
		},
		filters: {
			formatDate(time) {
				let date = new Date(time)
				return formatDate(date, 'yyyy-MM-dd hh:mm')
			}
		},
		events: {
			'ratingtype.select': function(type) {
				this.selectType = type
				this.$nextTick(() => {
          this.scroll.refresh()
        })
			},
			'content.toggle': function(type) {
				this.onlyContent = type
				this.$nextTick(() => {
          this.scroll.refresh()
        })
			}
		},
		components: {
			Star,
			Split,
			ratingselect
		}
	}
</script>
<style type="text/css" lang="less">
	@import '../../common/stylus/mixin.less';

	.ratings{
		position: absolute;
		width: 100%;
		left: 0px;
		top: 174px;
		bottom: 0px;
		overflow: hidden;
		.overview{
			display: flex;
			padding: 18px 0;
			.overview-left{
				flex: 0 0 137px;
				width: 137px;
				padding: 6px 0;
				border-right: 1px solid rgba(7,17,27,0.1);
				text-align: center;
				@media only screen and (max-width: 320px){
					flex: 0 0 120px;
					width: 120px;
				}
				.score{
					margin-bottom: 6px;
					line-height: 28px;
					font-size: 24px;
					color: rgb(255,153,0);
				}
				.title{
					line-height: 12px;
					margin-bottom: 8px;
					font-size: 12px;
					color: rgb(7,17,27);
				}
				.rank{
					line-height: 10px;
					font-size: 10px;
					color: rgb(147,153,159);
				}
			}
			.overview-right{
				flex: 1;
				padding: 6px 0 6px 24px;
				@media only screen and (max-width: 320px){
					padding-left: 6px;
				}
				.score-wrapper{
					// line-height: 18px;
					margin-bottom: 8px;
					font-size: 0;
					.title{
						display: inline-block;
						vertical-align: top;
						line-height: 18px;
						font-size: 12px;
						color: rgb(7,17,27);
					}
					.star{
						display: inline-block;
						// line-height: 18px;
						vertical-align: top;
						margin: 1px 12px 0;
					}
					.score{
						display: inline-block;
						line-height: 18px;
						vertical-align: top;
						font-size: 12px;
						color: rgb(255,153,0);
					}
				}
				.delivery-wrapper{
					font-size: 0px;
					.title{
						line-height: 18px;
						font-size: 12px;
						color: rgb(7,17,27);
					}
					.delivery{
						margin-left: 12px;
						font-size: 12px;
						color: rgb(147,153,159);
					}
				}
			}
		}
		.rating-wrapper{
			padding: 0 18px;
			.rating-item{
				display: flex;
				margin: 18px 0;
				.borderbottomOne(rgba(7,17,27,0.1));
				.avatar{
					flex: 0 0 28px;
					width: 28px;
					margin-right: 12px;
					img{
						border-radius: 50%;
					}
				}
				.content{
					position: relative;
					flex: 1;
					.name{
						margin-bottom: 4px;
						line-height: 12px;
						font-size: 12px;
						color: rgb(7,17,27)
					}
					.star-wrapper{
						margin-bottom: 6px;
						font-size: 0;
						.star{
							display: inline-block;
							margin-right: 6px;
							vertical-align: top;
							// line-height: 12px;
						}
						.delivery{
							display: inline-block;
							vertical-align: top;
							line-height: 12px;
							font-size: 10px;
							color:rgb(147,153,159);
						}
					}
					.text{
						margin-bottom: 8px;
						line-height: 18px;
						color: rgb(7,17,27);
						font-size: 12px;
					}
					.recommend{
						line-height: 16px;
						font-size: 0px;
						.icon-thumb_up,.item{
							display: inline-block;
							margin: 0 8px 4px 0;
							font-size: 10px;
						}
						.icon-thumb_up{
							color: rgb(0,160,220);
						}
						.item{
							padding: 0 6px;
							border: 1px solid rgba(7,17,27,0.1);
							border-radius: 1px;
							color: rgb(147,153,159);
							background: #fff;
						}
					}
					.time{
						position: absolute;
						top: 0;
						right: 0;
						line-height: 12px;
            font-size: 10px;
            color: rgb(147, 153, 159);
					}
				}
			}
		}
	}
</style>


