<template>
  <div class="seller" v-el:seller>
    <div class="seller-content">
      <div class="overview">
        <h1 class="title">{{seller.name}}</h1>
        <div class="desc border-1px">
          <star :size="36" :score="seller.score"></star>
          <span class="text">({{seller.ratingCount}})</span>
          <span class="text">月售{{seller.sellCount}}单</span>
        </div>
        <ul class="remark">
          <li class="block">
            <h2>起送价</h2>
            <div class="content">
              <span class="stress">{{seller.minPrice}}</span>元
            </div>
          </li>
          <li class="block">
            <h2>商家配送</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryPrice}}</span>元
            </div>
          </li>
          <li class="block">
            <h2>平均配送时间</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryTime}}</span>分钟
            </div>
          </li>
        </ul>
        <div class="favorite" @click="toggleFavorites($event)">
          <span class="icon-favorite" :class="{'active':favorite}"></span>
          <span class="text">{{favoriteText}}</span>
        </div>
      </div>
      <split></split>
      <div class="bulletin">
        <h1 class="title">公告与活动</h1>
        <div class="content-wrapper border-1px">
          <p class="content">{{seller.bulletin}}</p>
        </div>
        <ul v-if="seller.supports" class="supports">
          <li class="support-item border-1px" v-for="item in seller.supports">
            <span class="icon" :class="classMap[seller.supports[$index].type]"></span>
            <span class="text">{{seller.supports[$index].description}}</span>
          </li>
        </ul>
      </div>
      <split></split>
      <div class="pics">
        <h1 class="title">商家实景</h1>
        <div class="pic-wrapper" v-el:pic-wrapper>
          <ul class="pic-list" v-el:pic-list>
            <li class="pic-item" v-for="pic in seller.pics">
              <img :src="pic" width="120" height="90">
            </li>
          </ul>
        </div>
      </div>
      <split></split>
      <div class="info">
        <h1 class="title border-1px">商家信息</h1>
        <ul>
          <li class="info-item" v-for="info in seller.infos">{{info}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
	import star from '../star/star.vue'
	import split from '../split/split.vue'
	import BScroll from 'better-scroll'
	import {saveToLocal, loadFromLocal} from 'common/js/store'

	export default{
		props: {
			seller: {
				type: Object
			}
		},
		data() {
			return {
				favorite: (() => {
          return loadFromLocal(this.seller.id, 'favorite', false)
        })()
			}
		},
		computed: {
      favoriteText() {
        return this.favorite ? '已收藏' : '收藏'
      }
    },
		created() {
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
    },
    watch: {
      'seller'() {
        this._initScroll()
        this._initPics()
      }
    },
    ready() {
      this._initScroll()
      this._initPics()
      console.log(this.$route)
    },
    methods: {
      _initScroll() {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$els.seller, {
            click: true
          })
        } else {
          this.scroll.refresh()
        }
      },
      _initPics() {
        if (this.seller.pics) {
          let picWidth = 120
          let margin = 6
          let width = (picWidth + margin) * this.seller.pics.length - margin
          this.$els.picList.style.width = width + 'px'
          this.$nextTick(() => {
            if (!this.picScroll) {
              this.picScroll = new BScroll(this.$els.picWrapper, {
                scrollX: true,
                eventPassthrough: 'vertical'
              })
            } else {
              this.picScroll.refresh()
            }
          })
        }
      },
      toggleFavorites(event) {
        if (!event._constructed) {
          return
        }
        this.favorite = !this.favorite
        saveToLocal(this.seller.id, 'favorite', this.favorite)
      }
    },
		components: {
			star,
			split
		}
	}
</script>
<style type="text/css" lang="less">
	@import "../../common/stylus/mixin.less";
	.seller{
		position: absolute;
		top: 174px;
		bottom: 0;
		left: 0;
		width: 100%;
		overflow: hidden;
		.overview{
			position: relative;
			padding: 18px;
			.title{
				margin-bottom: 8px;
				line-height: 14px;
				color: rgb(7,17,27);
				font-size: 14px;
			}
			.desc{
				padding-bottom: 18px;
				//line-height: 18px;
				font-size: 0;
				.borderbottomOne(rgba(7,17,27,0.1));
				.star{
					display: inline-block;
					vertical-align: top;
					margin-right: 8px;
				}
				.text{
					line-height: 18px;
					margin-right: 12px;
					display: inline-block;
					vertical-align: top;
					font-size: 10px;
					color: rgb(77,85,93);
				}
			}
			.remark{
				display: flex;
				padding-top: 18px;
				.block{
					flex: 1;
					text-align: center;
					border-right: 1px solid rgba(7,17,27,0.1);
					&:last-child{
						border-right: none;
					}
					h2{
						margin-bottom: 4px;
						font-size: 10px;
						line-height: 10px;
						color: rgb(147,153,159);
					}
					.content{
						line-height: 24px;
						font-size: 10px;
						color: rgb(7,17,27);
					}
					.stress{
						font-size: 24px;
					}
				}
			}
			.favorite{
				position: absolute;
        width: 50px;
        right: 11px;
        top: 18px;
        text-align: center;
        .icon-favorite{
        	display: block;
          margin-bottom: 4px;
          line-height: 24px;
          font-size: 24px;
          color: #d4d6d9;
          &.active{
            color: rgb(240, 20, 20);
          }
        }  
        .text{
        	line-height: 10px;
          font-size: 10px;
          color: rgb(77, 85, 93);
        }         
			}    
		}
		.bulletin{
			padding: 18px 18px 0 18px;
      .title{
      	margin-bottom: 8px;
        line-height: 14px;
        color: rgb(7, 17, 27);
        font-size: 14px;
      }
      .content-wrapper{
      	padding: 0 12px 16px 12px;
        .borderbottomOne(rgba(7, 17, 27, 0.1));
        .content{
        	line-height: 24px;
          font-size: 12px;
          color: rgb(240, 20, 20);
        }       
      }
      .supports{
				// width: 80%;
				// margin: 0 auto;
				.support-item{
					padding: 16px 12px;
          .borderbottomOne(rgba(7, 17, 27, 0.1));
          font-size: 0;
					&:last-child{
						.borderNone;
					}
					.icon{
						display: inline-block;
						width: 16px;
						height: 16px;
						vertical-align: middle;
						margin-right: 6px;
						background-size: 16px 16px;
						background-repeat: no-repeat;

						&.decrease{
							.bg-image("../../components/seller/decrease_4@2x.png","../../components/seller/decrease_4@3x.png");
						}
						&.discount{
							.bg-image("../../components/seller/discount_4@2x.png","../../components/seller/discount_4@3x.png");
						}
						&.guarantee{
							.bg-image("../../components/seller/guarantee_4@2x.png","../../components/seller/guarantee_4@3x.png");
						}
						&.invoice{
							.bg-image("../../components/seller/invoice_4@2x.png","../../components/seller/invoice_4@3x.png");
						}
						&.special{
							.bg-image("../../components/seller/special_4@2x.png","../../components/seller/special_4@3x.png");
						}
					}
					.text{
						font-size: 12px;
						vertical-align: middle;
						// line-height: 16px;
					}
				}
			}
			 
		}
		.pics{
			padding: 18px;
      .title{
      	margin-bottom: 12px;
        line-height: 14px;
        color: rgb(7, 17, 27);
        font-size: 14px;
      }
      .pic-wrapper{
        width: 100%;
        overflow: hidden;
        white-space: nowrap;
        .pic-list{
        	font-size: 0;
          .pic-item{
          	display: inline-block;
            margin-right: 6px;
            width: 120px;
            height: 90px;
            &:last-child{
            	margin: 0   
           	}
          }   
        }
      }     
		}
		.info{
      padding: 18px 18px 0 18px;
      color: rgb(7, 17, 27);
      .title{
      	padding-bottom: 12px;
        line-height: 14px;
        .borderbottomOne(rgba(7, 17, 27, 0.1));
        font-size: 14px;
      } 
      .info-item{
      	padding: 16px 12px;
        line-height: 16px;
        .borderbottomOne(rgba(7, 17, 27, 0.1));
        font-size: 12px;
        &:last-child{
          .borderNone;
        }
      }  
		}
	}
</style>


