module.exports = " <div class=shopcart> <div class=content v-on:click=toggleList> <div class=contentLeft> <div class=logoWrapper> <div class=logo :class=\"{'highLight':totalCount>0}\"> <span class=icon-shopping_cart :class=\"{'highLight':totalCount>0}\"></span> </div> <div class=num v-show=\"totalCount>0\">{{totalCount}}</div> </div> <div class=price :class=\"{'highLight':totalPrice>0}\">￥{{totalPrice}}</div> <div class=description>另需配送费￥{{deliveryPrice}}元</div> </div> <div class=contentRight> <div class=pay :class=\"{'higtLight':payDesc==='去结算'}\" @click.stop.prevent=pay> {{payDesc}} </div> </div> <div class=ball-container> <div v-for=\"ball in balls\" v-show=ball.show transition=drop class=ball> <div class=\"inner inner-hook\"></div> </div> </div> </div> <div class=shopcart-list v-show=listShow transition=fold> <div class=list-header> <h1 class=title>购物车</h1> <span class=empty v-on:click=empty>清空</span> </div> <div class=list-content v-el:list-content> <ul> <li class=food v-for=\"food in selectFoods\"> <span class=name>{{food.name}}</span> <div class=price><span>￥{{food.price*food.count}}</span></div> <div class=cartcontrol-wrapper> <cartcontrol :food=food></cartcontrol> </div> </li> </ul> </div> </div> </div> <div class=list-mask @click=hideList v-show=listShow transition=fade></div> ";


