<template>
	<div class="food" v-show="showFlag" transition="move" v-el:food>
		<div class="food-content">
			<div class="image-header">
				<img :src="food.image">
				<div class="back" @click="hide">
					<i class="icon-arrow_lift"></i>
				</div>
			</div>
			<div class="content">
				<h1 class="title">{{food.name}}</h1>
				<div class="detail">
					<span class="sell-count">
						月售{{food.sellCount}}
					</span>
					<span class="rating">
						好评率{{food.rating}}
					</span>
				</div>
				<div class="price">
					<span class="newPrice">￥{{food.price}}</span><span v-show="food.oldPrice" class="oldPrice">{{food.oldPrice}}</span>
				</div>
				<div class="cartcontrol-wrapper">
					<cartcontrol :food="food"></cartcontrol>
				</div>
				<div class="buy" @click.stop.prevent="addFirst($event)" v-show="!food.count || food.count === 0" transition="fade">加入购物车</div>
			</div>
			<split v-show="food.info"></split>
			<div class="info" v-show="food.info">
        <h1 class="title">商品信息</h1>
        <p class="text">{{food.info}}</p>
      </div>
      <split v-show="food.rating"></split>
			<div class="rating">
				<h1 class="title">商品评价</h1>
				<ratingselect :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="food.ratings"></ratingselect>
				<div class="rating-wrapper">
					<ul v-show="food.ratings && food.ratings.length">
						<li v-for="rating in food.ratings" class="rating-item border-1px" v-show="needShow(rating.rateType,rating.text)">
							<div class="user">
								<div class="name">{{rating.username}}</div>
								<img :src="rating.avatar" class="avatar" width="12" height="12">
							</div>
							<div class="time">{{rating.rateTime | formatDate}}</div>
							<p class="text">
								<span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>
								{{rating.text}}
							</p>
						</li>
					</ul>
					<div class="no-rating" v-show="!food.ratings || !food.ratings.length">暂无评价</div>
				</div>
			</div>
		</div>	
	</div>
</template>
<script type="text/javascript">
	import BScroll from 'better-scroll'
	import Cartcontrol from '../cartcontrol/cartcontrol.vue'
	import Vue from 'vue'
	import Split from '../split/split.vue'
	import ratingselect from '../ratingselect/ratingselect.vue'
	import {formatDate} from '../../common/js/date.js'
	// const POSITIVE = 0
	// const NEGATIVE = 1
	const ALL = 2
	export default{
		props: {
			food: {
				type: Object
			}
		},
		data() {
			return {
				showFlag: false,
				selectType: ALL,
				onlyContent: true,
				desc: {
					all: '全部',
					positive: '推荐',
					negative: '吐槽'
				}
			}
		},
		methods: {
			needShow(type, text) {
				if (this.onlyContent && !text) {
					return false
				}
				if (this.selectType === ALL) {
					return true
				} else {
					return type === this.selectType
				}
			},
			show() {
				this.showFlag = true
				this.selectType = ALL
				this.onlyContent = true
				this.$nextTick(() => {
					if (!this.scroll) {
						this.scroll = new BScroll(this.$els.food, {
							click: true
						})
					} else {
						this.scroll.refresh()
					}
				})
			},
			hide() {
				this.showFlag = false
			},
			addFirst(event) {
				if (!event._constructed) {
					return
				}
				this.$dispatch('cart.add', event.target)
				Vue.set(this.food, 'count', 1)
			}
		},
		events: {
			'ratingtype.select': function(type) {
				this.selectType = type
				this.$nextTick(() => {
          this.scroll.refresh()
        })
			},
			'content.toggle': function(type) {
				this.onlyContent = type
				this.$nextTick(() => {
          this.scroll.refresh()
        })
			}
		},
		filters: {
			formatDate(time) {
				let date = new Date(time)
				return formatDate(date, 'yyyy-MM-dd hh:mm')
			}
		},
		components: {
			Cartcontrol,
			Split,
			ratingselect
		}
	}
</script>
<style type="text/css" lang="less">
	@import "../../common/stylus/icon.less";
	@import "../../common/stylus/mixin.less";
	.food{
		position: fixed;
		left: 0;
		top: 0;
		bottom: 48px;
		z-index: 30;
		width: 100%;
		background: #fff;
		transition: all 0.5s linear;
		// overflow-y: scroll;
		// &::-webkit-scrollbar {display:none}
		&.move-transition{
			transform: translate3d(0,0,0)
		}
		&.move-enter,&.move-leave{
			transform: translate3d(100%,0,0)
		}
		.food-content{
			// position: relative;
			.image-header{
				position: relative;
				width: 100%;
				height: 0px;
				padding-top: 100%;
				img{
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
				}
				.back{
					position: absolute;
					top: 10px;
					left: 0;
					.icon-arrow_lift{
						display: inline-block;
						padding: 10px;
						font-size: 20px;
						color:#fff;
					}
				}
			}
			.content{
				padding: 18px;
				position: relative;
				.title{
					line-height: 14px;
					height: 14px;
					margin-bottom: 8px;
					font-size: 14px;
					font-weight: 700;
					color: rgb(7,17,27);
				}
				.detail{
					margin-bottom: 18px;
					line-height: 10px;
					height: 10px;
					font-size: 0px;
					.sell-count,.rating{
						font-size: 10px;
						color: rgb(147,153,159);
					}
					.sell-count{
						margin-right: 12px;
					}
				}
				.price{
					font-weight: 700;
					line-height: 24px;
					.newPrice{
						margin-right: 8px;
						font-size: 14px;
						color: rgb(240,20,20);
					}
					.oldPrice{
						text-decoration: line-through;
						font-size: 10px;
						color: rgb(147,153,159);
					}
				}
				.cartcontrol-wrapper{
					position: absolute;
					right: 12px;
					bottom: 12px;
				}
				.buy{
					position: absolute;
					right: 18px;
					bottom: 18px;
					z-index: 10;
					height: 24px;
					line-height: 24px;
					padding: 0 12px;
					box-sizing: border-box;
					font-size: 10px;
					border-radius: 12px;
					color: #fff;
					background: rgb(0,120,220);
					&.fade-transition{
						transition: all 0.2s;
						opacity: 1; 
					}
					&.fade-enter,&.fade-leave{
						opacity: 0;
						// z-index: -1;
					}
				}
			}
			.info{
				padding: 18px;
				.title{
					line-height: 24px;
					margin-bottom: 6px;
					font-size: 14px;
					color: rgb(7,17,27);
				}
				.text{
					line-height: 24px;
					padding: 0 8px;
					font-size: 12px;
					color: rgb(77,85,93);
				}
			}
			.rating{
				padding-top: 18px;
				.title{
					line-height: 24px;
					margin-left: 18px;
					font-size: 14px;
					color: rgb(7,17,27);
				}
				.rating-wrapper{
					padding: 0 18px;
					.rating-item{
						position: relative;
						padding: 16px 0;
						.borderbottomOne(rgb(7,17,27,0.1));
					}
					.user{
						position: absolute;
						right: 0;
						top: 16px;
						font-size: 0px;
						line-height: 12px;
						.name{
							display: inline-block;
							vertical-align: middle;
							margin-right: 6px;
							font-size: 12px;
							color: rgb(147,153,159);
						}
						.avatar{
							border-radius: 50%;
							vertical-align: middle;
						}
					}
					.time{
						margin-bottom: 6px;
						line-height: 12px;
						font-size: 12px;
						color: rgb(147,153,159);
					}
					.text{
						line-height: 16px;
						font-size: 14px;
						color: rgb(7,17,27);
						.icon-thumb_up,.icon-thumb_up{
							display: inline-block;
							line-height: 16px;
							// margin-top: 2px;
							vertical-align: middle;
							margin-right: 4px;
							font-size: 14px;
						}
						.icon-thumb_up{
							color: rgb(0,160,220);
						}
						.icon-thumb_down{
							color: rgb(147,153,159);
						}
					}
					.no-rating{
						padding: 16px 0;
						font-size: 12px;
						color: rgb(147,153,159);
					}
				}
			}
		}
	}
</style>


